<template>
  <div class="login">
    <Top/>
    <p>一个账号，畅享所有服务！</p>
    <div class="container">
      <h3></h3>
      <form action="">
        <div class="ctrl">
          <input type="text" placeholder="手机号/邮箱/用户名" id="name">
          <p class="tip">{{ teltip }}</p>
        </div>
        <div class="ctrl">
          <input type="text" placeholder="请输入密码" id="password">
          <p class="tip">{{ passwordtip }}</p>
          <a href="https://sso.kuaidi100.com/sso/findpwd.jsp?redirectUri=https%3A%2F%2Fm.kuaidi100.com%2Fucenter.jsp" id="find">忘记密码？</a>
        </div>
      </form>
      <button class="btn-submit" @click="login">登录</button>
      <p class="tip">{{ tip }}</p>
      <div class="type">
        <a href="" class="bd">手机短信登录</a>
        <router-link class='fr' to="/reg">立即注册</router-link >       
      </div>
      <div class="divide">
        <span>快捷登录</span>
      </div>
      <div class="oauth">
        <a href="" class="qq"></a>
      </div>
    </div>
  </div>
</template>
<script>
  import Top from '@/components/top.vue'
  import axios from 'axios'
  export default {
    name :"login",
    data (){
      return{
        tel: '18365027720',
        password: '000000',
        tip: ''
      }
    },
    components:{
      Top
    },
    computed: {
    teltip () {
      if (this.tel === '') {
        return ''
      } else if (this.tel.length !== 11) {
        return '手机号码格式错误'
      } else {
        return ''
      }
    },
    passwordtip () {
      if (this.password === '') {
        return ''
      } else if (this.password.length < 6) {
        return '密码格式错误'
      } else {
        return ''
      }
    }
  },
  methods: {
    login () {
      console.log('11111')
      if (this.tel === '' || this.teltip !== '') {
        this.tip = '手机号格式错误'
        return
      }
      if (this.password === '' || this.passwordtip !== '') {
        this.tip = '密码格式错误'
        return
      }
      // 登陆
      axios.post('/users/login', {
        tel: this.tel,
        password: this.password
      }).then(res => {
        console.log(res.data)
        /***
         * 10086 未注册
         * 10100 密码错误
         * 10010 登陆成功
         */
        if (res.data.code === '10086') {
          this.tip = '该用户未注册，请先注册'
        } else if (res.data.code === '10100') {
          this.tip = '密码错误'
        } else {
          // 此时为登陆成功，获取token信息存入本地
          this.tip = ''
          // const token = res.data.token
          // localStorage.setItem('token', token)
          // localStorage.setItem('userid', res.data.userid)
          localStorage.setItem('username', res.data.username)
          localStorage.setItem('tel', res.data.tel)
          this.$router.back()
        }
      })
    }
  }
  }
</script>
<style>
    p{
    font-size: 1.2rem;
    margin: 15px 0;
    text-align: center;
    font-weight: normal;
    color: #555551;
  }
  .container{
    width:auto;
    margin: 0 auto;
    font-size: 14px;
    padding: 0 20px 0;
  }
  .container h3{
    margin: 10px 0;
    font-size: 1rem;
    text-align: center;
    font-weight: normal;
    line-height: 2em;
  }
  .container form{
    background: #FFF;
    display: block;
    padding: 10px 20px 0;
    border: 1px solid #e7ebef;
  }
  .container .ctrl{
    position: relative;
    padding: 10px 0;
    border-bottom: 1px solid #e7ebef;
  }
  .container input{
    font-size: .875rem;
    width: 100%;
    background: #FFF;
    outline: none;
    border: none;
    vertical-align: middle;
    line-height: 22px;
  }
  .container .ctrl a{
    font-size: 14px;
    position: absolute;
    right: 0;
    top: 10px;
    cursor: pointer;
    outline: none;
    text-decoration: none;
    color: #3278e6;
  }
  .container .btn-submit{
    padding: .5em 0;
    display: block;
    width: 100%;
    background: #fd7f24;
    color: #FFF;
    text-align: center;
    font-size: 16px;
    outline: none;
    border: none;
    margin-top: 10px;
    border-radius: 3px;
    cursor: pointer;
    /* vertical-align: middle; */
  }
  .container .type{
    padding: 10px;
    height:14px;
  }
  .container .type a.bd{
    float: left;
    color: #3278e6;
  }
  .container .type a.fr{
    float: right;
    color: #3278e6;
  }
  .container .divide{
    color: #888;
    margin: 10px 0;
    text-align: center;
    position: relative;
    border-top: 1px dashed #888;
  }
  .container .divide span{
    position: absolute;
    left: 50%;
    top: -1em;
    background: #f7fafc;
    padding: 0 10px;
    margin-left: -2.2em;
  }
  .container .oauth{
    color: #666;
    padding: 0 10px;
    height: 40px;
  }
  .container .oauth a {
    float:left;
    display: inline-block;
    vertical-align: middle;
    width: 35px;
    height: 35px;
    margin-right: 8px;
    background-image: url(https://cdn.kuaidi100.com/images/sso/login.png);
    background-repeat: no-repeat;
    background-position: -1px 0;
  };
</style>