<template>
  <div class="login">
    <Top/>
    <p>创建你的快递100账号</p>
    <div class="container">
      <form action="">
        <div class="ctrl">
          <input type="text" placeholder="请输入手机号/邮箱" id="name">
          <p class="tip">{{ teltip }}</p>
        </div>
        <div class="ctrl">
          <input type="text" placeholder="请输入验证码" id="code"> 
          <a href="" id="send">获取验证码</a>
        </div>
        <div class="ctrl">
          <input type="text" placeholder="请输入不少于6位密码" id="password">
          <p class="tip">{{ passwordtip }}</p>
        </div>
      </form>
      <!-- <div class="text-warn">请输入正确的手机号或邮箱</div> -->
      <button class="btn-submit" @click="register">注册</button>
      <div class="agreement">
        <i></i>
        <span>我已阅读并同意<a href="">《注册协议》</a>和<a href="">《隐私条例》</a></span>
      </div>
      <div class="type">
        <router-link class='fr' to="/login">已有账号，去登录</router-link >
      </div>
    </div>
  </div>
</template>
<script>
 import Top from '@/components/top.vue'
 import axios from 'axios'
  export default {
    name :"reg",
    data (){
      return{
        tel:18365027720,
        password:'000000',
        tip:""
      }
    },
    components:{
      Top
    },
    computed: {
      passwordtip () {
        if (this.password === '') {
          return ''
        } else if (this.password.length < 6) {
          return '密码格式错误'
        } else {
          return ''
        }
      },
      teltip () {
        if (this.tel === '') {
          return ''
        } else if (this.tel.length !== 11) {
          return '手机号码格式错误'
        } else {
          return ''
        }
      }
    },
    methods: {
      register () {
        if (this.tel === '' || this.teltip !== '') {
          this.tip = '请输入正确的手机号码'
          return
        }
        if (this.password === '' || this.passwordtip !== '') {
          this.tip = '请输入正确的密码'
          return
        }
        this.tip = ''
        console.log('可以注册了')
        axios.post('/users/register', {
          // username: this.username,
          tel: this.tel,
          password: this.password
        }).then(res => {
          console.log(res.data)
          if (res.data.code === '10000') {
            this.tip = '该用户已注册，请直接登陆'
          } else {
            this.tip = '注册成功'
          }
        })
      }
    }      
  }
</script>
<style>
  .container{
    width: auto;
    margin: 0 auto;
    font-size: 14px;
    padding: 0 20px 0;
  }
  .container a{
    color: #3278e6;
  }
  .container form{
    background: #FFF;
    display: block;
    padding: 10px 20px 0;
    border: 1px solid #e7ebef;
  }
  .container form .ctrl {
    position: relative;
    padding: 10px 0;
    border-bottom: 1px solid #e7ebef;
  }
  .container .ctrl input {
    font-size: .875rem;
    width: 100%;
    background: #FFF;
    outline: none;
    border: none;
    vertical-align: middle;
    line-height: 22px;
  }
  .container .ctrl a{
    font-size: 14px;
    position: absolute;
    right: 0;
    top: 10px;
    cursor: pointer;
    text-decoration: none;
  }
  .container .text-warn {
    display: none;
    opacity: 1;
    position: relative;
    color: #fd7f24;
    margin-top: 10px;
  }
  .container .btn-sbumit {
    padding: .5em 0;
    display: block;
    width: 100%;
    background: #fd7f24;
    color: #FFF;
    text-align: center;
    font-size: 16px;
    outline: none;
    border: none;
    margin-top: 10px;
    border-radius: 3px;
    cursor: pointer;
  }
  .container .agreement{
    color: #888;
    margin-top: 15px;
    margin-left: 5px;
  }
  .container .agreement i{
    display: inline-block;
    width: 1em;
    height: 1em;
    vertical-align: -2px;
    border: 1px solid #aaa;
    margin-right: 5px;
    cursor: pointer;
    border-radius: 2px;
  }
  .container .type{
    padding: 10px;
  }
  .container .type a{
    float: right;
  }
</style>